webpackJsonp([68432282983298],{

/***/ 282:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h3 id=\"前言\"><a href=\"#%E5%89%8D%E8%A8%80\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>前言</h3>\n<p>秋招提前批已经基本结束了，即将进入金九银十，正式的号角已经打响。春招，以及秋招提前批一路过来，断断续续也面了一些公司，自己在笔记上也有总结，甚至自己进行过一些猜题。发现基本问到的问题八九不离十，但是有些知识，特别是偏工程的知识点，如果没遇到过，很难产生深刻的印象。结合自己之前的笔记，也想在正式进入9月之前，整理一个面试题集系列，加深理解</p>\n<p>这是这个系列的第四篇，关于CSS</p>\n<h3 id=\"一、元素水平垂直居中\"><a href=\"#%E4%B8%80%E3%80%81%E5%85%83%E7%B4%A0%E6%B0%B4%E5%B9%B3%E5%9E%82%E7%9B%B4%E5%B1%85%E4%B8%AD\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>一、元素水平垂直居中</h3>\n<p>这个话题感觉被问烂了，网上已经有很多方法，我这里只给出常见且好记忆的几种方法</p>\n<h4 id=\"单行文字居中\"><a href=\"#%E5%8D%95%E8%A1%8C%E6%96%87%E5%AD%97%E5%B1%85%E4%B8%AD\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>单行文字居中</h4>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> red<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">height</span><span class=\"token punctuation\">:</span> 100px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">line-height</span><span class=\"token punctuation\">:</span> 100px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">text-align</span><span class=\"token punctuation\">:</span> center<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>理解单行文字居中的原理，我们首先需要理解<code class=\"language-text\">line-height</code>，这里要避免一个误区</p>\n<p>包括在我看过的<em>HTML and CSS, Design and Build Website</em>这本书，以及一些文档中，都还将CSS的<code class=\"language-text\">line-height</code>认为是两行文字基线之间的距离，在一些软件例如Photoshop, Word中确实是这样，但是CSS的<code class=\"language-text\">line-height</code>是这样的（<strong>no</strong>表示基线之间距离，<strong>yes</strong>才是CSS的<code class=\"language-text\">line-height</code>距离）</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blog/static/line-height-0301a379ebf7d2dfcbb995c4a2cfc7fa-a637f.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 750px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 43.99999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"line height\"\n        title=\"\"\n        src=\"/blog/static/line-height-0301a379ebf7d2dfcbb995c4a2cfc7fa-a637f.png\"\n        srcset=\"/blog/static/line-height-0301a379ebf7d2dfcbb995c4a2cfc7fa-1b868.png 213w,\n/blog/static/line-height-0301a379ebf7d2dfcbb995c4a2cfc7fa-b3c84.png 425w,\n/blog/static/line-height-0301a379ebf7d2dfcbb995c4a2cfc7fa-a637f.png 750w\"\n        sizes=\"(max-width: 750px) 100vw, 750px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<span><em>In CSS, the <code class=\"language-text\">line-height</code> is not the distance between baselines</em></span></p>\n<p>这样的话，设置了<code class=\"language-text\">line-height</code>等于容器<code class=\"language-text\">height</code>才能使单行文字居中</p>\n<h4 id=\"绝对定位已知居中元素宽高\"><a href=\"#%E7%BB%9D%E5%AF%B9%E5%AE%9A%E4%BD%8D%E5%B7%B2%E7%9F%A5%E5%B1%85%E4%B8%AD%E5%85%83%E7%B4%A0%E5%AE%BD%E9%AB%98\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>绝对定位(已知居中元素宽高)</h4>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> red<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">height</span><span class=\"token punctuation\">:</span> 200px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 400px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">position</span><span class=\"token punctuation\">:</span> absolute<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">right</span><span class=\"token punctuation\">:</span> 0<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">bottom</span><span class=\"token punctuation\">:</span> 0<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">top</span><span class=\"token punctuation\">:</span> 0<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">left</span><span class=\"token punctuation\">:</span> 0<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">margin</span><span class=\"token punctuation\">:</span> auto<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h4 id=\"绝对定位负边距位移已知居中元素宽高\"><a href=\"#%E7%BB%9D%E5%AF%B9%E5%AE%9A%E4%BD%8D%E8%B4%9F%E8%BE%B9%E8%B7%9D%E4%BD%8D%E7%A7%BB%E5%B7%B2%E7%9F%A5%E5%B1%85%E4%B8%AD%E5%85%83%E7%B4%A0%E5%AE%BD%E9%AB%98\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>绝对定位+负边距位移(已知居中元素宽高)</h4>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> red<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">height</span><span class=\"token punctuation\">:</span> 200px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 400px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">position</span><span class=\"token punctuation\">:</span> absolute<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">top</span><span class=\"token punctuation\">:</span> 50%<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">left</span><span class=\"token punctuation\">:</span> 50%<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">margin-top</span><span class=\"token punctuation\">:</span> -100px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">margin-left</span><span class=\"token punctuation\">:</span> -200px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h4 id=\"使用transform属性居中元素未知宽高\"><a href=\"#%E4%BD%BF%E7%94%A8transform%E5%B1%9E%E6%80%A7%E5%B1%85%E4%B8%AD%E5%85%83%E7%B4%A0%E6%9C%AA%E7%9F%A5%E5%AE%BD%E9%AB%98\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用transform属性(居中元素未知宽高)</h4>\n<p>和上一个类似，只不过使用<code class=\"language-text\">transform</code>属性进行位移</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> red<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">position</span><span class=\"token punctuation\">:</span> absolute<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">top</span><span class=\"token punctuation\">:</span> 50%<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">left</span><span class=\"token punctuation\">:</span> 50%<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">transform</span><span class=\"token punctuation\">:</span> <span class=\"token function\">translate</span><span class=\"token punctuation\">(</span>-50%, -50%<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h4 id=\"使用flex-boxdisplay-flex\"><a href=\"#%E4%BD%BF%E7%94%A8flex-boxdisplay-flex\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用flex-box(display: flex)</h4>\n<p><code class=\"language-text\">flex-direction</code> 默认值为<code class=\"language-text\">row</code>，此时<code class=\"language-text\">justify-content: center</code>用于水平居中元素，<code class=\"language-text\">align-items: center</code>用于垂直居中元素，如果设置<code class=\"language-text\">flex-direction: column</code>，则上面两个属性作用相反</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> flex<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">justify-content</span><span class=\"token punctuation\">:</span> center<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">align-items</span><span class=\"token punctuation\">:</span> center<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">height</span><span class=\"token punctuation\">:</span> 100%<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p><strong>扩展：</strong>需要注意<code class=\"language-text\">flex-box</code>还有一个属性叫做<code class=\"language-text\">align-content</code>，在默认情况下<code class=\"language-text\">flex-direction: row</code>，如果只有一行元素，则<code class=\"language-text\">align-content</code>不起作用，多行元素时，<code class=\"language-text\">align-content</code>可以调整行与行之间的距离，比如</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blog/static/align-items-and-align-content-71842d84a1c0147e552734824ac50fb7-9b205.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 676px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.325443786982255%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"align items and align content\"\n        title=\"\"\n        src=\"/blog/static/align-items-and-align-content-71842d84a1c0147e552734824ac50fb7-9b205.png\"\n        srcset=\"/blog/static/align-items-and-align-content-71842d84a1c0147e552734824ac50fb7-0224c.png 213w,\n/blog/static/align-items-and-align-content-71842d84a1c0147e552734824ac50fb7-ab527.png 425w,\n/blog/static/align-items-and-align-content-71842d84a1c0147e552734824ac50fb7-9b205.png 676w\"\n        sizes=\"(max-width: 676px) 100vw, 676px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<span>using <code class=\"language-text\">align-items: center</code> and <code class=\"language-text\">align-content: space-around</code></span></p>\n<h4 id=\"参考\"><a href=\"#%E5%8F%82%E8%80%83\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>参考</h4>\n<ol>\n<li><a href=\"https://iamvdo.me/en/blog/css-font-metrics-line-height-and-vertical-align#line-height-problems-and-beyond\">https://iamvdo.me/en/blog/css-font-metrics-line-height-and-vertical-align#line-height-problems-and-beyond</a></li>\n</ol>\n<h3 id=\"二、ie怪异盒模型和html正常盒模型\"><a href=\"#%E4%BA%8C%E3%80%81ie%E6%80%AA%E5%BC%82%E7%9B%92%E6%A8%A1%E5%9E%8B%E5%92%8Chtml%E6%AD%A3%E5%B8%B8%E7%9B%92%E6%A8%A1%E5%9E%8B\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>二、IE怪异盒模型和HTML正常盒模型</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blog/static/IE-box-model-and-W3C-box-model-0561c03dbb3285b4de03b69a81a41549-4befe.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 138.6%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"IE box model and W3C box model\"\n        title=\"\"\n        src=\"/blog/static/IE-box-model-and-W3C-box-model-0561c03dbb3285b4de03b69a81a41549-4befe.png\"\n        srcset=\"/blog/static/IE-box-model-and-W3C-box-model-0561c03dbb3285b4de03b69a81a41549-91460.png 213w,\n/blog/static/IE-box-model-and-W3C-box-model-0561c03dbb3285b4de03b69a81a41549-4a28b.png 425w,\n/blog/static/IE-box-model-and-W3C-box-model-0561c03dbb3285b4de03b69a81a41549-4befe.png 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>两种盒模型的主要区别在CSS属性<code class=\"language-text\">width</code>和<code class=\"language-text\">height</code>的计算方式上，拿<code class=\"language-text\">width</code>来举例</p>\n<blockquote>\n<p>IE Box Model: width = border + padding + content</p>\n<p>W3C Box Model: width = content</p>\n</blockquote>\n<p>默认使用的是W3C盒模型计算方式，如果我们要使用怪异盒模型，我们需要使用<code class=\"language-text\">box-sizing</code>这个属性，这个属性有两个值<code class=\"language-text\">content-box</code>和<code class=\"language-text\">border-box</code>，前一个值为默认值(W3C盒模型)，后一个值表示使用怪异模型</p>\n<h3 id=\"三、position有哪些属性\"><a href=\"#%E4%B8%89%E3%80%81position%E6%9C%89%E5%93%AA%E4%BA%9B%E5%B1%9E%E6%80%A7\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>三、position有哪些属性</h3>\n<p>总共四大类</p>\n<blockquote>\n<ol>\n<li>Positioned Elements, 除了<code class=\"language-text\">static</code></li>\n<li>Relatively Positioned Elements, <code class=\"language-text\">relative</code>，相对于自身本来的位置</li>\n<li>Absolute Positioned Elements, <code class=\"language-text\">absolute</code>和<code class=\"language-text\">fixed</code>，前者相对于第一个<em>positioned</em>父元素，后者相对于视窗</li>\n<li>Stickily Positioned Elements, <code class=\"language-text\">sticky</code></li>\n</ol>\n</blockquote>\n<p>这里主要说一下<code class=\"language-text\">sticky</code>属性，假设一个容器中某个元素使用了<code class=\"language-text\">position: sticky</code>，且容器高度超过了视窗高度（也就是可以滚动），假设对该元素设置<code class=\"language-text\">top: 10px</code>，随着容器的滚动，该元素不会移出视窗，在移动到距离视窗10px的位置会固定下来（类似<code class=\"language-text\">fixed</code>），直到该容器全部移出视窗，此时该元素才会随着容器一起移出</p>\n<iframe height='291' scrolling='no' title='Sticky positioning' src='//codepen.io/tianzhich/embed/RYQYdm/?height=291&theme-id=dark&default-tab=css,result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='https://codepen.io/tianzhich/pen/RYQYdm/'>Sticky positioning</a> by Tian Zhi (<a href='https://codepen.io/tianzhich'>@tianzhich</a>) on <a href='https://codepen.io'>CodePen</a>.\n</iframe>\n<h3 id=\"四、清除浮动的方式\"><a href=\"#%E5%9B%9B%E3%80%81%E6%B8%85%E9%99%A4%E6%B5%AE%E5%8A%A8%E7%9A%84%E6%96%B9%E5%BC%8F\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>四、清除浮动的方式</h3>\n<p>首先要清楚，浮动元素会对其后面的元素布局产生影响，所以，清除浮动指的不是清除已经浮动的元素的布局，而是清除前面浮动元素对自己产生的影响，有下面几种情况</p>\n<blockquote>\n<p><strong>浮动元素不占据文档流，因此后面的元素会当它不存在，但是有以下几点需要注意：</strong></p>\n<p><strong>1. 如果其兄弟元素是块级元素，会当它不存在而覆盖它</strong></p>\n<p><strong>2. 如果是行内元素，则是会围绕它</strong></p>\n<p><strong>3. 如果兄弟是块级元素但是其内容是行内元素，那么这些行内元素还是会围绕浮动元素</strong></p>\n</blockquote>\n<p>如果只是简单清除前面浮动元素对自己的影响，使用<code class=\"language-text\">clear</code>属性就能解决，<code class=\"language-text\">clear: left</code>表示将之前左浮动的元素视为正常元素，<code class=\"language-text\">clear</code>属性还有<code class=\"language-text\">both</code>，<code class=\"language-text\">right</code>值</p>\n<p>浮动元素还会对父元素产生影响，如果一个父元素中的子元素全部浮动，这个父元素高度会坍塌，也就是高度为0因为<strong>父元素如果没有设置高度，其高度是由它最后一个子元素的文档流位置决定的</strong></p>\n<p>如果需要使得父元素被撑开，可以使用如下几个方法</p>\n<ol>\n<li>\n<p>让父元素的最后一个子元素不再浮动，并且使用它来清除浮动</p>\n</li>\n<li>\n<p>基于上面一个方法，我们也可以使用伪元素达到这个目的，这叫做clearfix</p>\n</li>\n</ol>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">#container::after</span> <span class=\"token punctuation\">{</span> \n  <span class=\"token property\">content</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"\"</span><span class=\"token punctuation\">;</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> block<span class=\"token punctuation\">;</span> \n  <span class=\"token property\">clear</span><span class=\"token punctuation\">:</span> both<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<ol start=\"3\">\n<li>最后一个方法让父元素使用<code class=\"language-text\">overflow: auto</code>或者<code class=\"language-text\">overflow: hidden</code>创建一个BFC(<em>Block Formatting Context</em>)，BFC可以包含浮动元素，让区域有高度</li>\n</ol>\n<h3 id=\"五、bfc是什么\"><a href=\"#%E4%BA%94%E3%80%81bfc%E6%98%AF%E4%BB%80%E4%B9%88\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>五、BFC是什么</h3>\n<p>BFC，也是被问到较多的一个问题，这里说说它的原理和一些使用场景</p>\n<p><strong>BFC(Block Formatting Context, 块级格式上下文)</strong>，可以理解为是元素创建的一块区域，这块区域可以包含它里面的所有元素(包括浮动元素)</p>\n<h4 id=\"创建方式\"><a href=\"#%E5%88%9B%E5%BB%BA%E6%96%B9%E5%BC%8F\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>创建方式</h4>\n<p>BFC创建方式有很多，可以直接<a href=\"https://developer.mozilla.org/en-US/docs/Web/Guide/CSS/Block_formatting_context\">查看MDN的解释</a></p>\n<p>这里说几种常见的方式，这几种方式也可以用来解决如下场景问题</p>\n<ol>\n<li>块级元素设置<code class=\"language-text\">overflow</code>为除默认值<code class=\"language-text\">visible</code>之外的任一值</li>\n<li>绝对定位元素(<code class=\"language-text\">absolute</code>或<code class=\"language-text\">fixed</code>)</li>\n<li>浮动元素</li>\n<li>flex布局元素(<code class=\"language-text\">display: flex</code>下的子元素)</li>\n<li>显式生成BFC，使<code class=\"language-text\">display: flow-root</code>，这种方法还处于草案阶段，需要考虑兼容问题</li>\n</ol>\n<h4 id=\"包含浮动元素\"><a href=\"#%E5%8C%85%E5%90%AB%E6%B5%AE%E5%8A%A8%E5%85%83%E7%B4%A0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>包含浮动元素</h4>\n<p>BFC最初被我知道是用来解决浮动元素无法撑开父元素的问题</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">#container</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">overflow</span><span class=\"token punctuation\">:</span> auto<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* 或者 overflow: hidden */</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h4 id=\"解决边距折叠\"><a href=\"#%E8%A7%A3%E5%86%B3%E8%BE%B9%E8%B7%9D%E6%8A%98%E5%8F%A0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>解决边距折叠</h4>\n<p>它还可以解决一个重要的问题，就是<a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Box_Model/Mastering_margin_collapsing\"><em>Margin Collapsing</em>（边距折叠）</a></p>\n<p>边距折叠有以下几种情况</p>\n<ol>\n<li>假设两个<code class=\"language-text\">div</code>，上面的<code class=\"language-text\">div</code>定义了<code class=\"language-text\">margin-bottom</code>，下面的<code class=\"language-text\">div</code>定义了<code class=\"language-text\">margin-top</code>，它们之间的<code class=\"language-text\">margin</code>由两者的较大值决定</li>\n</ol>\n<div class=\"gatsby-highlight\" data-language=\"html\">\n      <pre class=\"language-html\"><code class=\"language-html\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>container<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">></span></span>The bottom margin of this heading is collapsed...<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>...with the top margin of this paragraph.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">h1</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-bottom</span><span class=\"token punctuation\">:</span> 15px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-top</span><span class=\"token punctuation\">:</span> 10px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p><img src=\"https://magnusbenoni.com/content/images/2016/01/Adjacent.png\"></p>\n<p>解决方案1：让它们浮动起来，然后再用伪元素清除浮动(下面为了简单起见我直接让后者清除)</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">h1</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-bottom</span><span class=\"token punctuation\">:</span> 15px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">float</span><span class=\"token punctuation\">:</span> left<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-top</span><span class=\"token punctuation\">:</span> 10px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">clear</span><span class=\"token punctuation\">:</span> left<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">float</span><span class=\"token punctuation\">:</span> left<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>解决方案2：父级元素使用<code class=\"language-text\">display: flex</code>，<strong>这种布局根本不存在margin collapsing的问题</strong></p>\n<p>上面的方案1不是好的解决办法，或者是使用绝对定位，这都打乱了正常的文档流。但是我发现直接设置<code class=\"language-text\">overflow: auto</code>或者是使用<code class=\"language-text\">display: flow-root</code>都无法解决，所以最好直接使用flex布局</p>\n<ol start=\"2\">\n<li>父级container元素下的第一个元素会折叠<code class=\"language-text\">margin-top</code>，最后一个元素会折叠<code class=\"language-text\">margin-bottom</code>，表现为第一个元素使用<code class=\"language-text\">margin-top</code>和最后一个元素使用<code class=\"language-text\">margin-bottom</code>都无法撑开父元素。而且如果父元素自己也设置了<code class=\"language-text\">margin-top</code>或者<code class=\"language-text\">margin-bottom</code>，会与第一个元素或者最后一个元素设置的<code class=\"language-text\">margin</code>发生重叠</li>\n</ol>\n<div class=\"gatsby-highlight\" data-language=\"html\">\n      <pre class=\"language-html\"><code class=\"language-html\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>The margin of this paragraph ends up outside the parent.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-top</span><span class=\"token punctuation\">:</span> 10px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-top</span><span class=\"token punctuation\">:</span> 20px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p><img src=\"https://magnusbenoni.com/content/images/2016/01/Parent-and-child.png\"></p>\n<p>解决方案：</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token comment\">/* 方案1 */</span>\n<span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">overflow</span><span class=\"token punctuation\">:</span> auto<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token comment\">/* 方案2 */</span>\n<span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> flex<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token comment\">/* 方案3 */</span>\n<span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> flow-root<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<ol start=\"3\">\n<li>空元素上下边距折叠</li>\n</ol>\n<div class=\"gatsby-highlight\" data-language=\"html\">\n      <pre class=\"language-html\"><code class=\"language-html\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>container<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">></span></span>Top block.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>empty<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">></span></span>Bottom block.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">#empty</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-top</span><span class=\"token punctuation\">:</span> 50px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">margin-bottom</span><span class=\"token punctuation\">:</span> 50px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p><img src=\"https://magnusbenoni.com/content/images/2016/01/Empty-3.png\"></p>\n<p>解决方案：</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token comment\">/* 方案1, 注意这里使用 flow-root和overflow都无效*/</span>\n<span class=\"token selector\">#container</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> flex<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">flex-direction</span><span class=\"token punctuation\">:</span> column<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token comment\">/* 方案2 */</span>\n<span class=\"token selector\">#empty</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> flex<span class=\"token punctuation\">;</span> <span class=\"token comment\">/* 或者display: flow-root 或者overflow: auto */</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h4 id=\"使用overflow创建bfc的缺点\"><a href=\"#%E4%BD%BF%E7%94%A8overflow%E5%88%9B%E5%BB%BAbfc%E7%9A%84%E7%BC%BA%E7%82%B9\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>使用<code class=\"language-text\">overflow</code>创建BFC的缺点</h4>\n<p>使用<code class=\"language-text\">overflow</code>创建的BFC，虽然解决了浮动元素的问题，但是在边距折叠中，只有第二三种情况可以使用这种方式，而且，使用它创建BFC，还有如下缺点</p>\n<ol>\n<li>上面的情况父元素都没有设置高度，如果父元素需要设置高度，则使用<code class=\"language-text\">overflow: scroll</code>创建BFC可能会出现不必要的滚动条；同理，使用<code class=\"language-text\">overflow: hidden</code>则可能会截断重要的内容</li>\n<li>道理和第一点类似，<code class=\"language-text\">overflow</code>属性本来就是用来告诉开发人员如果文档内容溢出应该如何处理的问题，如果开发人员不理解BFC，他很难知道一段代码是用来创建BFC还是处理溢出的情况</li>\n</ol>\n<p>因此草案<a href=\"https://drafts.csswg.org/css-display/#valdef-display-flow-root\">CSS Display Module Level 3</a>提出了<code class=\"language-text\">display: flow-root</code>用来规范创建BFC的方式</p>\n<h4 id=\"参考-1\"><a href=\"#%E5%8F%82%E8%80%83-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>参考</h4>\n<ol>\n<li><a href=\"https://magnusbenoni.com/collapsing-margins-css/\">https://magnusbenoni.com/collapsing-margins-css/</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/Guide/CSS/Block_formatting_context\">https://developer.mozilla.org/en-US/docs/Web/Guide/CSS/Block<em>formatting</em>context</a></li>\n</ol>\n<h3 id=\"六、repaint重绘和reflow重排\"><a href=\"#%E5%85%AD%E3%80%81repaint%E9%87%8D%E7%BB%98%E5%92%8Creflow%E9%87%8D%E6%8E%92\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>六、Repaint(重绘)和Reflow(重排)</h3>\n<p>重绘是指元素的显示样式发生改变，不涉及到布局位置的改变，例如改变<code class=\"language-text\">background-color</code>，<code class=\"language-text\">visibility</code>等等；而重排则会影响到元素的布局位置，例如设置<code class=\"language-text\">display: none</code>，改变字体大小，改变浏览器窗口大小等等</p>\n<p>重排会影响到其兄弟元素，父子元素都发生改变，会影响页面加载的性能，以下几种做法可以尽量减小这种影响</p>\n<ol>\n<li>改变元素样式时尽可能针对到某个特定元素，避免扩大影响的范围</li>\n<li>避免多次使用JS改变行内样式，每次更改行内样式，如果涉及到位置变化，都会引发一次Layout，我们可以更改其父元素样式，引发一次Layout即可</li>\n<li>尽量在<strong>绝对定位</strong>的元素上使用动画，动画触发多次Layout，比较影响性能，为了不让其影响到其他元素，应该将其设置在脱离正常文档流的元素上</li>\n<li>减小table布局的使用，单元格里一个小小的改变将会影响整个table的布局</li>\n</ol>\n<p>关于页面加载以及Layout这一过程的细节，可以看我的另一篇文章，<a href=\"https://tianzhich.github.io/blog/learning/2018/09/20/How-a-Webpage-is-Displayed/\">深入理解浏览器解析渲染HTML</a></p>","frontmatter":{"title":"面试的那些题-CSS篇","date":"September 08, 2018","excerpt":"最近想复习总结下面试中遇到的所有面试题，以及自己觉得可能出现的面试题，又或者是我觉得不错的需要掌握的知识。这是Master the FED Interviews系列的第四篇：关于CSS","contents":[{"text":"前言","subContents":null},{"text":"一、元素水平垂直居中","subContents":null},{"text":"二、ie怪异盒模型和html正常盒模型","subContents":null},{"text":"三、position有哪些属性","subContents":null},{"text":"四、清除浮动的方式","subContents":null},{"text":"五、bfc是什么","subContents":null}]}}},"pathContext":{"slug":"/learning/2018/09/08/Master-the-FED-Interviews-CSS/"}}

/***/ })

});


